import { useState, useEffect } from "react";
import { Link } from 'react-router-dom'
import axios from 'axios';
import swal from 'sweetalert';
import {
  Card,
  Divider,
  CardContent,
  CardHeader,
  Box,
  Icon,
  IconButton,
  Button,
  styled,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  CircularProgress
} from "@mui/material";
// import { LoadingButton } from '@mui/lab';
import GroupIcon from '@mui/icons-material/Group';
import { Breadcrumb, SimpleCard } from "app/components";



const StyledTable = styled(Table)(() => ({
  whiteSpace: "pre",
  "& thead": {
    "& tr": { "& th": { paddingLeft: 0, paddingRight: 0 } },
  },
  "& tbody": {
    "& tr": { "& td": { paddingLeft: 0, textTransform: "capitalize" } },
  },
}));

const Container = styled("div")(({ theme }) => (
  {
    margin: "30px",
    [theme.breakpoints.down("sm")]: { margin: "16px" },
    "& .breadcrumb": {
      marginBottom: "30px",
      [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
    },
  }
));
const ContentBox = styled(Box)(() => ({
  height: '100%',
  padding: '32px',
  position: 'relative',
  // background: 'rgba(0, 0, 0, 0.01)',
}));

const OverallInventorysList = () => {
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(20);
  const [medicines, setMedicines] = useState(false);
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    fetchData();
  }, []);

  /**
   * Fetching data from server.
   */
  const fetchData = async () => {
    setLoading(true);
    try {
      await axios.get('/sanctum/csrf-cookie').then(response => {
        axios.post('/api/admin/get-medicine-inventory-list')
          .then(response => {
            if (response.data.response_code === 200) {
              setMedicines(response.data.data);
            } else if (response.data.response_code === 401) {

            } else {

            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.error(error);
          });
      });
    } catch (e) {
      setLoading(false);
    }
  }

  const __rowDelete = (e, row_id, name) => {
    swal({
      title: "Are you sure?",
      text: "You will not be able to recover \"" + name + "\" Medicine!",
      icon: "warning",
      buttons: [
        'No, cancel it!',
        'Yes, I am sure!'
      ],
      dangerMode: true,
    }).then(function (isConfirm) {
      if (isConfirm) {
        try {
          axios.get('/sanctum/csrf-cookie').then(response => {
            axios.post('/api/client/delete-medicine', {
              'row_id': row_id
            })
              .then(response => {
                if (response.data.response_code === 200) {
                  fetchData();
                  swal({
                    title: 'Done!',
                    text: response.data.message,
                    icon: 'success'
                  })
                } else if (response.data.response_code === 412) {
                  swal({
                    title: "Errors!",
                    text: response.data.message,
                    icon: "error",
                  });
                } else {
                  swal({
                    title: "Errors!",
                    text: response.data.message,
                    icon: "error",
                  });
                }
                setLoading(false);
              })
              .catch(function (error) {
                setLoading(false);
                console.error(error);
              });
          });
        } catch (e) {
          setLoading(false);
        }
      } else {
        swal("Cancelled", "Your data is safe :)", "error");
      }
    });
  }

  const handleChangePage = (_, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };

  return (
    <>
      <Container>
        <Box className="breadcrumb">
          <Breadcrumb routeSegments={[{ name: "dashboard", path: "/dashboard" }, { name: "Medicines" }]} />
        </Box>
        <Card title="Medicines">
          <CardHeader
            // avatar={<GroupIcon>A</GroupIcon>}
            title="Medicines"
            titleTypographyProps={{
              variant: "h4",
              fontSize: '16px',
              fontWeight: '600',
              textTransform: 'uppercase',
              textAlign: 'center'
            }}
          />
          <Divider />
          <CardContent>
            <Box width="100%" overflow="auto">
              <StyledTable>
                <TableHead>
                  <TableRow>
                    <TableCell align="left" sx={{ width: '50px' }}>Sr. No.</TableCell>
                    <TableCell align="left">Product Name</TableCell>
                    <TableCell align="left">Category</TableCell>
                    <TableCell align="left">Brand</TableCell>
                    {/* <TableCell align="left">Description</TableCell> */}
                    <TableCell align="left">Unit Packing</TableCell>
                    <TableCell align="left">Expiry Date</TableCell>
                    <TableCell align="center">HSN/SAC Code</TableCell>
                    <TableCell align="center">Batch No.</TableCell>
                    <TableCell align="center">Status</TableCell>
                    {/* <TableCell align="center">Created At</TableCell>
                    <TableCell align="center">Updated At</TableCell> */}
                    <TableCell align="center" sx={{ width: '250px' }}>Action</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {medicines ? medicines
                    .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                    .map((row, index) => (
                      <TableRow key={index}>

                        <TableCell align="center">{index + 1}</TableCell>
                        <TableCell align="left">{row.product_name}</TableCell>
                        <TableCell align="left">{row.category_name}</TableCell>
                        <TableCell align="left">{row.brand_name}</TableCell>
                        {/* <TableCell align="left">{row.description}</TableCell> */}
                        <TableCell align="center">{row.unit_packing}</TableCell>
                        <TableCell align="left">{row.expiry_date}</TableCell>
                        <TableCell align="center">{row.hsn_sac_code}</TableCell>
                        <TableCell align="center">{row.batch_no}</TableCell>
                        <TableCell align="center">
                          {(() => {
                            switch (row.status) {
                              case 'approved':
                                return <Button color="success" variant="contained" size="small">Approved</Button>
                              case 'pending':
                                return <Button color="warning" variant="contained" size="small">Pending</Button>
                              case 'rejected':
                                return <Button color="error" variant="contained" size="small">Rejected</Button>
                              default:
                                return null
                            }
                          })()}
                        </TableCell>
                        {/* <TableCell align="center">{row.created_at}</TableCell>
                        <TableCell align="center">{row.updated_at}</TableCell> */}


                        <TableCell title="View" align="right" sx={{ width: '200px' }}>
                          <Button component={Link}
                            to={`/dashboard/medicine/view/${row.id}`}
                            color="success" variant="outlined" size="small" sx={{ m: .5 }}>
                            Approve
                          </Button>
                          <Button component={Link}
                            to={`/dashboard/medicine/view/${row.id}`}
                            color="primary" variant="outlined" size="small" sx={{ m: .5 }}>
                            View
                          </Button>
                          <Button title="Edit" component={Link}
                            to={`/dashboard/medicine/edit-stock/${row.id}`}
                            color="info" variant="outlined" size="small" sx={{ m: .5 }}>
                            Edit
                          </Button>
                          <Button title="Delete" onClick={(e) => __rowDelete(e, row.id, row.product_name)}
                            color="error" variant="outlined" size="small" sx={{ m: .5 }}>
                            Delete
                          </Button>
                        </TableCell>
                      </TableRow>
                    )) : <TableRow><TableCell align="center" colSpan={10}><CircularProgress className="progress" /></TableCell></TableRow>}
                </TableBody>
              </StyledTable>
              {medicines ?
                <TablePagination
                  sx={{ px: 2 }}
                  page={page}
                  component="div"
                  rowsPerPage={rowsPerPage}
                  count={medicines.length}
                  onPageChange={handleChangePage}
                  rowsPerPageOptions={[5, 10, 25]}
                  onRowsPerPageChange={handleChangeRowsPerPage}
                  nextIconButtonProps={{ "aria-label": "Next Page" }}
                  backIconButtonProps={{ "aria-label": "Previous Page" }}
                />
                : <>

                </>
              }
            </Box>
          </CardContent>
        </Card>
      </Container>
    </>

  );
};

export default OverallInventorysList;
